import { Grid, Stack, TextField, TextFieldProps } from '@mui/material';
import dayjs, { Dayjs } from 'dayjs';
import { useMemo, useState } from 'react';
import { DesktopDatePicker, LocalizationProvider } from '@mui/x-date-pickers';

import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import MonthlyFateChart from './MothlyFateChart';
import {IGraphProps} from "../Layout.contract";

const MonthlyFate = ({birthDate, setBirthDate}: IGraphProps) => {
  const [months] = useState<number>(12);
  const [year, setYear] = useState<Dayjs>(dayjs());
  const [month, setMonth] = useState<number>(1);

  const handleYearChange = (value: Dayjs | null) => {
    if (!value) {
      setYear(dayjs());
    }

    setYear(value!);
  };

  const dayMonthValue = useMemo(() => {
    if (!birthDate) {
      return '';
    }

    return `${birthDate?.format('D')}${birthDate?.format('MM')}`;
  }, [birthDate]);

  const chartNumbers = useMemo(() => {
    if (!dayMonthValue || !birthDate || !months || !year) {
      return '';
    }

    return (
      parseInt(dayMonthValue, 10) *
      birthDate.year() *
      months *
      year.year()
    ).toString();
  }, [dayMonthValue, birthDate, months, year]);

  return (
    <Grid container spacing={2}>
      <Grid item xs={12} md={3}>
        <LocalizationProvider dateAdapter={AdapterDayjs} adapterLocale="ru">
          <Grid container spacing={2} alignItems="center" justifyItems="center" direction="column">
            <Grid item container spacing={-5} xs={12} justifyContent="center" direction="row" alignItems="center" justifyItems="center">
              <DesktopDatePicker
                  label=" "
                  inputFormat="DD.MM.YYYY"
                  value={birthDate}
                  onChange={setBirthDate}
                  renderInput={(
                      params: JSX.IntrinsicAttributes & TextFieldProps
                  ) => <TextField {...params} fullWidth />}
              />
            </Grid>
            <Grid item container spacing={-5} xs={12} justifyContent="center" direction="row" alignItems="center" justifyItems="center">
              <DesktopDatePicker
                  label=" "
                  inputFormat="YYYY"
                  views={['year']}
                  value={year}
                  onChange={handleYearChange}
                  renderInput={(
                      params: JSX.IntrinsicAttributes & TextFieldProps
                  ) => <TextField {...params} fullWidth />}
              />
            </Grid>
            <Grid item container spacing={-5} xs={12} justifyContent="center" direction="row" alignItems="center" justifyItems="center">
              <TextField
                  label="/"
                  disabled
                  aria-readonly
                  value={dayMonthValue}
                  fullWidth
              />
            </Grid>

            <Grid item container spacing={-5} xs={12} justifyContent="center" direction="row" alignItems="center" justifyItems="center">
              <TextField
                  label=" "
                  type="number"
                  disabled
                  value={months}
                  fullWidth
              />
            </Grid>

            <Grid item container spacing={-5} xs={12} justifyContent="center" direction="row" alignItems="center" justifyItems="center">
              <TextField
                  label=" "
                  type="text"
                  disabled
                  value={chartNumbers}
                  fullWidth
              />
            </Grid>
          </Grid>
        </LocalizationProvider>
      </Grid>
      <Grid item xs={12} md={6}>
        <MonthlyFateChart chartNumbers={chartNumbers} />
      </Grid>
    </Grid>
  );
};

export default MonthlyFate;
