import dayjs from 'dayjs';
import { useMemo } from 'react';
import { IMonthlyFateChartProps } from './MonthlyFate.contract';
import ChartGraph from '../ChartGraph/ChartGraph';

const MonthlyFateChart = ({ chartNumbers }: IMonthlyFateChartProps) => {
  const perMonthArray = useMemo<number[]>(() => {
    const res = chartNumbers.split('').map((val) => parseInt(val, 10));

    while (res.length < 12) {
      res.push(res[0]);
    }

    return res;
  }, [chartNumbers]);

  return <ChartGraph defaultColors={['#a5a5a5']} data={[perMonthArray]} labels={dayjs.monthsShort()} />;
};

export default MonthlyFateChart;
